import * as React from "react";
import { motion } from "framer-motion";
import { MenuItem } from "./MenuItem";

const variants = {
  open: {
    transition: { staggerChildren: 0.07, delayChildren: 0.2 }, zIndex: 200
  },
  closed: {
    transition: { delay:1, staggerChildren: 0.05, staggerDirection: -1 }, zIndex: -100,
    
  }
};


export const Navigation = ({toggle, dis}) => (
    
          
        
  <motion.ul variants={variants} className="unor">
  
    {itemIds.map((i) => (
      <MenuItem toggle={toggle} dis={dis} i={i} key={i}  />
    ))}
  </motion.ul>
  
);

const itemIds = [0, 1, 2, 3, 4];

export default Navigation;